<template>
<div class="content">
  <div class="header">
    <div class="current">
      当前积分：
      <span>7500</span>
    </div>
    <van-icon name="arrow" />
  </div>
  <div class="box">
    <ul class="list">
      <li class="list-item" v-for="item in list" :key="item.id">
        <div class="item-con">
          <div class="imgbox">
            <img :src="item.image" alt="item.name" />
          </div>
          <div class="name">{{ item.name }}</div>
          <div class="info">
            <div class="price_current">{{ item.integral }}积分+{{ item.price_current }}元</div>
            <div class="price_before">市场价：￥{{ item.price_before }}</div>
          </div>
        </div>
      </li>
    </ul>
  </div>
  <div class="more">更多好物，敬请期待</div>
  <div class="tabs">
    <div class="btn">积分商城</div>
    <div class="btn">我的兑换</div>
  </div>
</div>
</template>

<script>
export default {
  data() {
    return {
      list:[
        {
          id: 1,
          name: "摩米士iFan小凉便携风扇",
          image: require("@/assets/integral/img1.png"),
          price_before: "129.00",
          price_current: "9.90",
          integral:"800"
        },
        {
          id: 2,
          name: "VISIONS康宁晶钻锅2.2L",
          image: require("@/assets/integral/img2.png"),
          price_before: "968.00",
          price_current: "39.90",
          integral:"800"
        },
        {
          id: 3,
          image: require("@/assets/integral/img3.png"),
          name: "米技微电脑触控果蔬料理机 ",
          price_before: "878.00",
          price_current: "39.90",
          integral:"800"
        },
        {
          id: 4,
          name: "北欧欧慕香薰加湿器",
          image: require("@/assets/integral/img4.png"),
          price_before: "486.00",
          price_current: "99.90",
          integral:"800"
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.content{
  margin-bottom: 100px;
  .header{
    height: 140px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color:#FFF;
    .current{
      font-size: 26px;
      color: #222;
      padding-left:24px;
      span{
        color: #CFA02D;
        font-size: 36px;
      }
    }
    .van-icon{
      color: #CFA02D;
      margin-right: 30px;
      font-size: 30px;
    }
  }
  .box{
    padding: 0 24px;
    .list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 20px;
    .list-item {
      width: 50%;
      margin-bottom: 14px;
      &:nth-of-type(odd) {
        .item-con {
          margin-right: 7px;
        }
      }
      &:nth-of-type(even) {
        .item-con {
          margin-left: 7px;
        }
      }
      .item-con {
        background-color: #fff;
        .imgbox {
          img {
            width: 100%;
          }
        }
        .name {
          height: 76px;
          margin: 6px 18px 0 14px;
          color: #333;
          font-size: 28px;
        }
        .info {
          padding: 12px 0px 26px 14px;
          .price_current {
            color: #CFA02D;
            font-size: 28px;
            margin-bottom: 6px;
          }
          .price_before {
            color: #999;
            font-size: 20px;
          }
        }
      }
    }
  }
  }
  .tabs{
    display: flex;
    height: 100px;
    position: fixed;
    bottom: 0;
    background-color:#FFF;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    font-size: 28px;
    .btn{
      box-sizing: border-box;
      &:nth-of-type(2){
        border-left: thin solid #D7D7D7;
        color:#888;
      }
      height: 60px;
      line-height: 60px;
      flex: 1;
      text-align: center;
    }
  }
}
</style>



